<template>
  <div
    id="myTestimonial"
    class="testimonial carousel slide carousel-dark"
    data-bs-ride="carousel"
  >
    <div class="carousel-indicators pb-4">
      <button
        v-for="index in 3"
        :key="index"
        :data-bs-slide-to="index - 1"
        :aria-label="'Slide ' + index"
        :class="{ active: index == 1 }"
        data-bs-target="#myTestimonial"
        type="button"
      ></button>
    </div>
    <div class="carousel-inner">
      <div
        v-for="item in testimonialItems"
        :key="item.id"
        :class="{ active: item.id == 1 }"
        class="carousel-item"
        data-bs-interval="5000"
      >
        <div class="container">
          <div class="row">
            <div class="col-lg-12">
              <div class="carousel-caption row justify-content-center">
                <div class="col-lg-7">
                  <div class="testimonial__text mx-5">
                    <img
                      src="data:image/webp;base64,"
                    />
                    <p>“{{ item.content }}”</p>
                    <h4>{{ item.name }}</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <button
      class="carousel-control-prev"
      type="button"
      data-bs-target="#myTestimonial"
      data-bs-slide="prev"
    >
      <span class="carousel-control-prev-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Previous</span>
    </button>
    <button
      class="carousel-control-next"
      type="button"
      data-bs-target="#myTestimonial"
      data-bs-slide="next"
    >
      <span class="carousel-control-next-icon" aria-hidden="true"></span>
      <span class="visually-hidden">Next</span>
    </button>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "AppTestimonial",
  data: function () {
    return {
      testimonialItems: null,
    };
  },
  created: function () {
    // API endpoint name: testimonial-items
    axios
      .get(
        `${process.env.VUE_APP_ROOT_API}/ba79d80e-fd4e-4084-a851-2eaa13e0e68a`
      )
      .then((response) => (this.testimonialItems = response.data))
      .catch((error) => console.log(error));
  },
};
</script>
